//BANNER SECTION
.main-banner {
	background: url("../img/banner-img.jpg") 0 0 no-repeat;
	min-height: 700px;
	background-size: cover;
	position: relative;
	ul {
		margin-top: 15px;
		@include columns(2 8em);
		li {
			color: #373737;
			font-size: 16px;
			font-weight: 400;
			line-height: 35px;
			i {
				color: #6e6e6e;
				display: inline-block;
				margin-right: 5px;
			}
		}
	}
	hgroup {
		margin-top: 95px;
	}
}
.get-started {
	margin-top: 50px;
	>span {
		display: inline-block;
		margin-left: 20px;
		color: #6e6e6e;
		font-size: 16px;
		font-weight: 300;
		@media (max-width: 1023px) {
			display: block;
		}
		strong {
			color: #ff8208;
			font-size: 29px;
			font-weight: 700;
		}
		em {
			font-style: normal;
			color: #ff8208;
			font-size: 24px;
			font-weight: 400;
		}
	}
}
.plan-detailes {
	display: inline-block;
	background: #1c69cd;
	border: none;
	color: $white;
	font-size: 16px;
	font-weight: 400;
	z-index: 100;
	position: absolute;
	left: 0;
	right: 0;
	bottom: -20px;
	width: 163px;
	margin: 0 auto;
	padding: 10px 0;
	text-align: center;
	@include border-radius(28px);
	&.collapsed {
		bottom: 90px;
		@include transition(all 0.5s ease-in-out);
	}
	&:after {
		content: "\f077";
		display: inline-block;
		padding-left: 10px;
		color: $white;
		font: normal normal normal 14px/1 FontAwesome;
	}
}

//PLAN DETAILS
.plan-details {
	background: #1c69cc url("../img/plan-details-bg.png") 0 0 no-repeat;
	background-size: cover;
	color: $white;
	padding: 45px 0 100px;
	h3 {
		font-size: 22px;
		font-weight: 700;
	}
	p {
		font-size: 16px;
		font-weight: 300;
		line-height: 26px;
		padding: 0 30px;
		@media (max-width: 767px) {
			margin-bottom: 30px;
		}
	}
}

//FIND DOMAIN
.find-domainname {
	position: relative;
	&:before {
		content: "";
		background: url("../img/cloud.png") 0 0 no-repeat;
		background-size: cover;
		height: 107px;
		position: absolute;
		top: -106px;
		left: 0;
		right: 0;
		width: 100%;
