//MyCompanyWeb 
.company-web {
	background: url("../img/company-web-bg.png") 0 0 no-repeat;
	background-size: cover;
	padding: 30px 0px;
	@media (min-width: 768px) {
		padding: 100px 0 65px;
	}
	.container {
		@media (min-width: 768px) {
			padding: 0;
		}
	}
	h2 {
		color: $white;
		font-size: 36px;
		font-weight: 700;
	}
	p {
		color: white;
		font-size: 20px;
		font-weight: 300;
	}
	&-left {
		@media (min-width: 768px) {
			border-right: 1px solid #657388;
			padding-right: 30px;
			padding-left: 0;
		}
	}
	&-right {
		@media (min-width: 768px) {
			padding-left: 30px;
			padding-right: 0;
		}
	}
	&-block {
		text-align: left;
		position: relative;
		margin-bottom: 60px;
		&:before {
			content: "";
			width: 76px;
			height: 76px;
			margin-right: 15px;
			@include border-radius(50%);
			float: left;
		}
		h4 {
			color: $white;
			font-size: 22px;
			font-weight: 700;
			margin-top: 0;
		}
		p {
			color: $white;
			font-size: 16px;
			font-weight: 300;
			line-height: 25px;
		}
		&-text {
			float: left;
			width: 78%;
			@media screen and (min-width: 768px) and (max-width: 1200px) {
				width: 73%;
			}
		}
		&-builder {
			&:before {
				content: "";
				background: #3fd25e url("../img/builder-icon.png") 50% no-repeat;
			}
		}
		&-support {
			&:before {
				content: "";
				background: #ff8421 url("../img/support-icon.png") 50% no-repeat;
			}
		}
		&-transfering {
			margin-bottom: 0;
			&:before {
				content: "";
				background: #3ad9bd url("../img/transfer-icon.png") 50% no-repeat;
			}
		}
		&-seo {
			&:before {
				content: "";
				background: #218fff url("../img/seo-icon.png") 50% no-repeat;
			}
		}
		&-email {
			&:before {
				content: "";
				background: #f6d20c url("../img/email-icon.png") 50% no-repeat;
			}
		}
		&-installer {
			margin-bottom: 0;
			&:before {
				content: "";
				background: #fd7758 url("../img/installer-icon.png") 50% no-repeat;
			}
		}
	}
}
.features {
	margin: 40px 0 50px;
}

//LOVE MyCompanyWeb 
