//FEEDBACK
.feedbacks {
	background: url("../img/feedback-bg.png") 0 0 no-repeat;
	background-size: cover;
	padding: 30px 0 60px;
	.carousel-wrapper {
		position: relative;
	}
	h2 {
		color: $white;
		font-size: 36px;
		font-weight: 700;
		margin-bottom: 70px;
	}
}
.owl-carousel {
	&.owl-feedback {
		max-width: 650px;
		margin: 0 auto;
		position: static;
		.owl-item {
			>div {
				text-align: center;
			}
			&.center {
				.photo-bgr {
					img {
						opacity: 1;
					}
				}
			}
		}
	}
	.owl-prev {
		@include transform(translateY(-50%));
		position: absolute;
		left: 0;
		top: 50%;
		@media (max-width: 768px) {
			width: 21px;
			height: 49px;
			top: 60%;
			left: 0;
			img {
				width: 100%;
			}
		}
	}
	.owl-next {
		@include transform(translateY(-50%));
		position: absolute;
		right: 0;
		top: 50%;
		@media (max-width: 768px) {
			width: 21px;
			height: 49px;

			top: 60%;
			right: 0;
			img {
				width: 100%;

			}
		}
	}
}
.owl-carousel-coments {
	p {
		max-width: 800px;
		margin: 40px auto 0;
		color: #fffbfb;
		font-size: 20px;
		font-weight: 300;
		line-height: 30px;
		@media (max-width: 1023px) {
			margin: 20px auto 0;
			font-size: 14px;
			line-height: 20px;
			padding: 0 40px;
		}
		span {
			display: block;
			text-align: center;
			margin-top: 40px;
		}
	}
}
.photo-bgr {
	width: 113px;
	height: 113px;
	@include border-radius(50%);
	display: inline-block;
	img {
		@include border-radius(50%);
		opacity: 0.5;
	}
}

