//Looking for something Different? 
.looking-for-different {
	position: relative;
	padding: 50px 0 5px;
	background: #e7f1fa;
	@media (min-width: 1023px) {
		background: #e7f1fa url("../img/something-diff-bg1.jpg") 0 bottom no-repeat;
		background-size: 118% 215px;
		&:before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			background: url("../img/something-diff-bg.png") center 50px no-repeat;
			background-size: cover;
			height: 215px;
			width: 100%;
			z-index: 5;
		}
	}
	h2 {
		color: #1f1f1f;
		font-size: 36px;
		font-weight: 700;
		line-height: 30px;
	}
	hgroup {
		h1 {
			color: $white;
			font-size: 26px;
			font-weight: 700;
			line-height: 30px;
			margin: 0;
		}
		h2 {
			color: $white;
			font-size: 15px;
			font-weight: 300;
			line-height: 30px;
			margin: 0;
		}
	}
	.panel-group {
		margin-top: 54px;
		.panel {
			background: none;
			border: none;
			box-shadow: none;
			display: inline-block;
			max-width: 327px;
			+.panel {
				margin: 0;
			}
			@media (max-width: 1023px) {
				margin-bottom: 20px!important;
			}
			&.reseller-hosting {
				.panel-heading {
					background-color: #08a48a;
				}
				.panel-body {
					.price {
						background-color: #13c6a8;
					}
					ul {
						li {
							i {
								color: #13c6a8;
							}
						}
					}
				}
			}
			&.vps-hosting {
				position: relative;
				@media (min-width: 1200px) {
					&:before {
						content: "";
						width: 62px;
						height: 37px;
						position: absolute;
						top: 102px;
						left: -90px;
						background: url("../img/cloud1.png") 0 0 no-repeat;
					}
				}
			}
			&.dedicated-hosting {
				position: relative;
				@media (min-width: 1200px) {
					&:before {
						content: "";
						width: 99px;
						height: 59px;
						position: absolute;
						top: -80px;
						right: -110px;

						background: url("../img/cloud2.png") 0 0 no-repeat;
					}
				}
			}
		}
		.panel-heading {
			background-color: #1871be;
			@include border-radius(10px 10px 0 0);
			&.collapsed {
				@media (max-width: 767px) {
					@include border-radius(10px);

				}
			}
		}
		.panel-body {
			border: none!important;
			background: $white;
			padding: 0;
			@include border-radius(0 0 10px 10px);
			@media screen and (min-width: 768px) and (max-width: 1023px) {
				padding: 0 0 30px;
			}
			@media (min-width: 1024px) {
				padding: 0 0 100px;
			}
			.price {
				background: #1f96fc;
				text-align: center;
				padding: 22px;
				.price-wrapper {
					padding: 10px 0;
					border-top: 1px solid rgba(255,255,255,0.5);
					border-bottom: 1px solid rgba(255,255,255,0.5);
				}
				p {
					display: inline-block;
					text-align: center;
					span {
						display: block;
						font-size: 16px;
						font-weight: 400;
						line-height: 1;
						text-align: left;
						color: $white;
						+span {
							color: $white;
							font-size: 58px;
							line-height: 1;
							font-weight: 700;
						}
					}
				}
			}
			ul {
				text-align: left;
				li {
					border-bottom: 1px solid #e1e1e1;
					padding: 15px;
					span {
						display: inline-block;
						width: 90%;
						text-align: center;
						vertical-align: middle;
					}
					i {
						display: inline-block;
						vertical-align: middle;
						width: 10%;
						color: #1f96fc;
						font-size: 16px;
					}
				}
			}
		}
		.btn-orange {
			color: white;
			font-size: 16px;
			font-weight: 700;
			padding: 15px;
			margin-top: 24px;
			position: relative;
			z-index: 100;
			@media (max-width: 768px) {
				width: 70%;
				margin: 28px auto 38px;
			} 
		}
	}
}
.btn-toggle {
	background: url("../img/toggle-btn.png") 0 0 no-repeat;
	width: 62px;
	height: 37px;
	position: absolute;
	left: 0;
	right: 0;
	bottom: -36px;
	margin: 0 auto;
	z-index: 100;
	&:hover, &.active {
		background: url("../img/toggle-btn.png") 0 0 no-repeat;
		box-shadow: none;
	}
	i {
		display: inline-block;
		vertical-align: middle;
	}
}

