.btn {
	@include transition(all 0.5s ease-in-out);
}
.btn-orange {
	display: inline-block;
	background-color: $orange;
	@include border-radius(37px);
	color: $white;
	font-size: 22px;
	font-weight: 700;
	padding: 0;
	span {
		line-height: 1;
		padding: 22px;
	}
	i {
		display: inline-block;
		background-color: #d46902;
		padding: 22px;
		@include border-radius(0 37px 37px 0);
	}
	&:hover {
		color: $white;
		background-color: lighten($orange, 10%);
		i {
			background-color: lighten(#d46902, 1%);
		}
	}
}
.btns-group {
	.btns {
		display: inline-block;
		font-size: 18px;
		font-weight: 700;
		@include border-radius(32px);
		padding: 12px 10px;
		@media (min-width: 768px) {
			min-width: 183px;
			padding: 20px;
		}
		&:hover {
			text-decoration: none;
			opacity: 0.95;
		}
	}
}
.btn-white {
	background: $white;
	color: #606060;
	margin-left: 40px;
}
.back-to-top {
	display: block;
	position: fixed;
	bottom: 30px;
	left: 30px;
	background: $blue;
	border: none;
	color: $white;
	width: 50px;
	height: 50px;
	@include border-radius(50%);
	font-size: 20px;
	text-align: center;
	z-index: 200;
	display:none;
	i {
		display: inline-block;
		line-height: 45px;
	}
	&:hover, &:focus {
		background: lighten($blue, 10%);
		color: $white;
	}
}