.search {
	input[type="search"] {
		background-color: #f2f1f1;
		@include border-radius(28px 0 0 28px);
		box-shadow: none;
		border: none;
		display: inline-block;
		color: #373737;
		font-size: 18px;
		font-weight: 300;
		width: 80%;
		padding: 28px;
		@include placeholder {
			color: #a7a7a7;
		}
	}
	.btn-orange {
		display: inline-block;
		width: 20%;
		@include border-radius(0 28px 28px 0);
		margin-left: -5px;
		font-size: 18px;
		font-weight: 700;
		padding: 18px;
		line-height: 1;
		vertical-align: top;
	}
}
.form-group-email {
	margin-bottom: 0;
	label {
		display: block;
		text-align: center;
		color: $white;
		font-size: 16px;
		font-weight: 400;
		margin-bottom: 13px;
	}
	.form-control {
		background-color: #437edc;
		@include border-radius(25px 0 0 25px);
		border: none;
		color: $white;
		font-size: 14px;
		font-weight: 400;
		line-height: 50px;
		height: 50px;
		display: inline-block;
		width: 65%;
		padding: 6px 25px;
		@include placeholder {
			color: #e8e8e8;
		}
	}
}
.btn-submit {
	background: $orange;
	@include border-radius(0 25px 25px 0);
	margin-left: -4px;
	color: $white;
	font-size: 16px;
	font-weight: 700;
	border: none;
	padding: 14px 25px;
	position: relative;
	top: -1px;
	width: 35%;
	&:hover {
		background: lighten($orange, 10%);
		color: $white;
	}
}



