@media screen and (min-width: 320px) and (max-width: 1200px) {
	.menu-col {
		text-align: center;
		margin-bottom: 30px;
	}
	.footer-menu {
		padding-top: 0;
	}
	.main-banner {
		background: url("../img/banner-img-mb.jpg") 0 0 no-repeat;
		background-size: cover;
		&-content {
			display: inline-block;
			float: right;
		}
	}
	.table-responsive {
		border: none;
		margin: 0;
	}
	.company-web {
		padding-top: 100px;
	}
}
@media screen and (max-width: 414px) {
	.main-banner {
		background: url("../img/banner-img-mb.jpg") 0 0 no-repeat;
		background-size: cover;
		&-content {
			width: 100%;
		}
	}
	.search {
		input[type="search"] {
			font-size: 14px;
			padding: 28px 10px;
		}
		.btn-orange {
			font-size: 14px;
			font-weight: 700;
			padding: 20px 4px;
		}
	}
	//MyCompanyWeb 
	.company-web {
		.container {
			@media (min-width: 768px) {
				padding: 0;
			}
		}
		h2 {
			color: $white;
			font-size: 36px;
			font-weight: 700;
		}
		p {
			color: white;
			font-size: 20px;
			font-weight: 300;
		}
		&-left {
			@media (min-width: 768px) {
				border-right: 1px solid #657388;
				padding-right: 30px;
				padding-left: 0;
			}
		}
		&-right {
			@media (min-width: 768px) {
				padding-left: 30px;
				padding-right: 0;
			}
		}
		&-block {
			text-align: left;
			position: relative;
			margin-bottom: 60px;
			&:before {
				content: "";
				width: 66px;
				height: 66px;
				margin-right: 10px;
				@include border-radius(50%);
				float: left;
			}
			h4 {
				color: $white;
				font-size: 22px;
				font-weight: 700;
				margin-top: 0;
			}
			p {
				color: $white;
				font-size: 16px;
				font-weight: 300;
				line-height: 25px;
			}
			&-text {
				float: left;
				width: 70%;
			}
		}
	}
}
@media screen and (min-width: 415px) and (max-width: 667px) {
	.main-banner {
		background: url("../img/banner-img-mb.jpg") 0 0 no-repeat;
		background-size: cover;
		&-content {
			width: 62%;
		}
	}
}
@media screen and (min-width: 1025px) and (max-width: 1200px) {
	//Start Building Your Website Today! 
	.start-building {
		text-align: center;
		padding: 48px 0;
		h2 {
			color: $white;
			font-size: 25px;
			line-height: 35px;
			font-weight: 300;
			display: block;
			vertical-align: middle;
			margin: 0;
			strong {
				font-weight: 700;
				text-transform: uppercase;
			}
		}
		.get-started {
			display: inline-block;
			float: none;
			margin-top: 20px;
		}
	}
	.start-price {
		display: block;
		margin-left: 0;
		text-align: center;
		span {
			display: inline-block;
			color: #fefefe;
			margin-top: 10px;
			font-size: 18px;
			font-weight: 400;
		}
		sup {
			color: #fefefe;
			font-size: 24px;
			font-weight: 400;
		}
		strong {
			color: #ffce08;
			font-size: 48px;
			font-weight: 700;
			line-height: 1;
		}
		em {
			font-style: normal;
			color: #fefefe;
			font-size: 24px;
			font-weight: 400;
		}
	}
}
@media screen and (max-width: 1024px) {
	//MENU
	.navbar-default {
		.navbar-header {
			display: block;
			width: 100%;
			margin: 0;
		}
		.navbar-nav {
			display: block;
			width: 100%;
			>li {
				display: block;
				float: none;
			}
		}
		.navbar-toggle {
			display: block;
		}
		.collapse {
			display: none;
			&.in {
			    display: block!important;
			}
		}
		.navbar-collapse {
			border: none;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			padding: 0;
			z-index: 1;
			background: $white;
			ul {
				margin-top: 90px;
				padding: 0 15px;
				border-top: 1px solid $blue;
			}
		}
	}
	//Start Building Your Website Today! 
	.start-building {
		text-align: center;
		padding: 48px 0;
		h2 {
			color: $white;
			font-size: 30px;
			line-height: 35px;
			font-weight: 300;
			display: block;
			vertical-align: middle;
			margin: 0;
			strong {
				font-weight: 700;
				text-transform: uppercase;
			}
		}
		.get-started {
			display: inline-block;
			float: none;
			margin-top: 20px;
		}
	}
	.start-price {
		display: block;
		margin-left: 0;
		text-align: center;
		span {
			display: inline-block;
			color: #fefefe;
			margin-top: 10px;
			font-size: 18px;
			font-weight: 400;
		}
		sup {
			color: #fefefe;
			font-size: 24px;
			font-weight: 400;
		}
		strong {
			color: #ffce08;
			font-size: 48px;
			font-weight: 700;
			line-height: 1;
		}
		em {
			font-style: normal;
			color: #fefefe;
			font-size: 24px;
			font-weight: 400;
		}
	}

}


@media (min-width: 768px) {
	.navbar-collapse.collapse {
	    display: none!important;
	    height: auto!important;
	    padding-bottom: 0;
	    overflow: visible!important;
	}
	.lg-show {
		display: block;
	}
}
@media (min-width: 1025px) {
	.navbar-collapse.collapse {
	    display: block!important;
	    height: auto!important;
	    padding-bottom: 0;
	    overflow: visible!important;
	}
}