.logo {
	position: relative;
	display: inline-block;
	padding: 25px 0;
  line-height: 100px;
  height: auto;
  max-width: 324px;
  @media screen and (max-width: 767px) {
    max-width: 200px;
  }
}
.main-menu {
	background: transparent;
	border: none;
  border-top: 6px solid $blue;
	z-index: 10;
	margin: 0;
  @include border-radius(0);
  box-shadow: 0 0 3px rgba(17, 17, 17, 0.53);
	.navbar-nav {
    margin: 0;
		@media screen and (min-width: 1025px) {
			float: right;
		}
		li {
			a {
				color: $grey;
        font-size: 15px;
        font-weight: 600;
        text-transform: uppercase;
				padding: 37px 20px;
        border-right: 1px solid $light-grey;
        @media screen and (max-width: 1024px) {
          padding: 10px;
          border: none;
        }
        @media screen and (min-width: 1025px) and (max-width: 1240px) {
          padding: 37px 10px;
          font-size: 14px;
        }
				&:hover {
					color: $blue;
				}
			}
      &:first-child {
        a {
          border-left: 1px solid $light-grey;
          @media screen and (max-width: 1024px) {
            border: none;
          }
        }
      }
      &.active {
        a {
          color: $blue;
        }
      }
		}
	}
	.navbar-header {
		position: relative;
		z-index: 10;
	}
	.navbar-toggle {
		border: none;
		background-color: transparent;
		.toggle {
			display: inline-block;
			vertical-align: middle;
			margin: 0;
			.icon-bar {
				background-color: $green;
			}
			&-text {
				display: inline-block;
				vertical-align: middle;
				font-weight: 700;
				color: $black;
				margin-right: 10px;
			}
		}
		&.burger-menu {
		  position: relative;
		  padding: 18px 15px;
		  height: 40px;
		  margin: 0 auto;
		  color: $black;
		  text-transform: uppercase;
		  font-weight: 700;
		  background: $white;
		  @include border-radius(2px);
		  &.is-open {
		  	background: transparent;
		  }
		}
	}
  &.navbar-default-inner {
      position: absolute;
      &.navbar-scrolling {
        background: transparent;
      }
  }
}
.navbar-collapse {
	padding: 0;
}

//BURGER 

.burger-click-region {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: -7px;
  width: 30px;
  margin-left: 15px;
  cursor: pointer;
}

$menu-animation-duration: 400ms;
$menu-animation-timing:   ease-out;

.burger-menu-piece {
  display: block;
  position: absolute;
  width: 36px;
  border-top: 3px solid $blue;
  transform-origin: 50% 50%;
  transition: transform $menu-animation-duration $menu-animation-timing;
  
  &:nth-child(1) {
    top: 0;
  }
  
  &:nth-child(2) {
    top: 10px;
    opacity: 1;
    transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
    -webkit-transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
    -moz-transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
    -ms-transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
    -o-transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
  }
  
  &:nth-child(3) {
    top: 20px;
  }
  
  .active & {
      &:nth-child(1) {
        animation: burger-open-top $menu-animation-duration $menu-animation-timing forwards;
        -webkit-animation: burger-open-top $menu-animation-duration $menu-animation-timing forwards;
        -moz-animation: burger-open-top $menu-animation-duration $menu-animation-timing forwards;
        -o-animation: burger-open-top $menu-animation-duration $menu-animation-timing forwards;
      	border-top: 3px solid $blue;
      }
    
      &:nth-child(2) {
        opacity: 0;
        transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
        -webkit-transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
        -moz-transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
        -ms-transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
        -o-transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
      }
    
      &:nth-child(3) {
        animation: burger-open-bot $menu-animation-duration $menu-animation-timing forwards;
        -webkit-animation: burger-open-bot $menu-animation-duration $menu-animation-timing forwards;
        -moz-animation: burger-open-bot $menu-animation-duration $menu-animation-timing forwards;
        -o-animation: burger-open-bot $menu-animation-duration $menu-animation-timing forwards;
      	border-top: 3px solid $blue;
      }
  }
  
  .closing & {
    &:nth-child(1) {
      animation: burger-close-top $menu-animation-duration $menu-animation-timing forwards;
      -webkit-animation: burger-close-top $menu-animation-duration $menu-animation-timing forwards;
      -moz-animation: burger-close-top $menu-animation-duration $menu-animation-timing forwards;
      -o-animation: burger-close-top $menu-animation-duration $menu-animation-timing forwards;
    }
    
    &:nth-child(3) {
       animation: burger-close-bot $menu-animation-duration $menu-animation-timing forwards;
      -webkit-animation: burger-close-bot $menu-animation-duration $menu-animation-timing forwards;
      -moz-animation: burger-close-bot $menu-animation-duration $menu-animation-timing forwards;
      -o-animation: burger-close-bot $menu-animation-duration $menu-animation-timing forwards;
    }
  }
}

@include keyframes(burger-open-top) {
  50% {
    @include transform(translate3d(0, 10px, 0));
  }
  100% {
    @include transform(translate3d(0, 10px, 0) rotate(45deg));
  }
}

@include keyframes(burger-open-bot) {
  50% {
    @include transform(translate3d(0, -10px, 0));
  }
  100% {
    @include transform(translate3d(0, -10px, 0) rotate(-45deg));
  }
}

@include keyframes(burger-close-top) {
  0% {
    @include transform(translate3d(0, 10px, 0) rotate(45deg));
  }
  50% {
    @include transform(translate3d(0, 10px, 0) rotate(0deg));
  }
  100% {
    @include transform(translate3d(0, 0, 0));
  }
}

@include keyframes(burger-close-bot) {
  0% {
    @include transform(translate3d(0, -8px, 0) rotate(-45deg));
  }
  50% {
    @include transform(translate3d(0, -8px, 0) rotate(0deg));
  }
  100% {
    @include transform(translate3d(0, 0, 0));
  }
}


