.footer {
	color: $white;
	background: $blue url("../img/footer-bg.png") 0 0 no-repeat;
	background-size: cover;
    @media screen and (min-width: 1024px) {
		height: 555px;
		position: absolute;
	    bottom: 0;
	    width: 100%;
	}
	h2 {
		font-size: 16px;
		font-weight: 700;
		line-height: 24px;
		text-transform: uppercase;
		margin-bottom: 10px;
		span {
			color: #91c9f4;
		}
	}
	h3 {
		color: $white;
		font-size: 16px;
		font-weight: 700;
		line-height: 24px;
		text-transform: uppercase;
		margin: 0 0 14px;
	}
	a {
		color: $white;
		font-size: 16px;
		font-weight: 400;
		line-height: 30px;
	}
}
.footer-bottom {
	background: #25293c;
	padding: 25px 0;
	@media screen and (min-width: 1024px) {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
	}
	p {
		color: #8f93a4;
		font-size: 13px;
		font-weight: 400;
		margin: 0;
	}

	ul {
		float: right;
		li {
			display: inline-block;
			a {
				display: inline-block;
				color: #8f93a4;
				font-size: 13px;
				font-weight: 400;
				padding: 0;
				position: relative;
				line-height: 1;
				&:after {
					content: "";
					width: 1px;
					height: 13px;
					float: left;
					background: #8f93a4;
					margin: 0 10px 0 5px;
				}
			}
			&:first-child {
				a {
					&:after {
						content: normal;
					}
				}
			}
		}
	}
}
.footer-menu {
	padding: 30px 0 5px;
	img {
		margin-top: 15px;
	}
}
.copyrights {
	text-align: left;
	@media (max-width: 768px) {
		text-align: center;
	}
}
.footer-content {
	padding: 37px 0 0;
}
.contact-us {
	padding-bottom: 35px;
	@media screen and (min-width: 1024px) {
		border-bottom: 1px solid #7198d2;
	}
}
.contact-us-col {
	display: inline-block;
}
a.drop-us-line {
	display: block;
	color: white;
	font-size: 23px;
	font-weight: 400;
	padding-top: 4px;
	margin-bottom: 10px;	
}
.tel {
	color: #bdd3f5;
	font-size: 18px;
	font-weight: 400;
	a {
		display: inline-block;
		padding-left: 10px;
		color: #ffce08;
		font-size: 27px;
		font-weight: 700;
		&:hover {
			text-decoration: none;
		}
	}
}
.menu-col {
	padding-left: 0;
}
.footer-img {
	padding-right: 0;
	img {
		float: right;
	}
}
.socials {
	padding-top: 10px;
	li {
		display: inline-block;
		margin-right: 12px;
		a {
			display: block;
			text-align: center;
			width: 40px;
			height: 40px;
			@include border-radius(50%);
			background-color: #3c589f;
			&:hover {
				background-color: lighten(#3c589f, 10%);
			}
			i {
				line-height: 40px;
				font-size: 20px;
			}
			&.twitter {
				background-color: #35a6e5;
				&:hover {
					background-color: lighten(#35a6e5, 10%);
				}
			}
			&.linkedin {
				background-color: #0e91c5;
				&:hover {
					background-color: lighten(#0e91c5, 10%);
				}
			}
			&.g-plus {
				background-color: #d64530;
				&:hover {
					background-color: lighten(#d64530, 10%);
				}
			}
		}
	}
}